
:root {
  --primary: #39f;
  --success: #0c6;
  --warning: #f90;
  --danger: #f30;
  --text: #737373;
  --text-dark: #595959;
  --text-light: #a6a6a6;
  --text-disabled: #bfbfbf;
  --border: #d9d9d9;
  --border-dark: #c9c9c9;
  --border-light: #e0e0e0;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
*:before,
*:after {
  box-sizing: border-box;
}
*:focus {
  outline: 0;
}
:-webkit-autofill {
  box-shadow: 0 0 0 10rem #fff inset;
  -webkit-text-fill-color: #574751;
  color: #574751;
}
::-webkit-input-placeholder {
  color: var(--text-light);
}
html {
  font-size: 10px;
  line-height: 1.42857143;
  color: var(--text);
}
body,
a {
  font-family: 'PingFang SC', 'Helvetica Neue', 'Helvetica', 'STHeitiSC-Light', Arial, sans-serif;
  font-size: 1.4rem;
}
